.name {
    color: #2BBC94;
    text-align: center;
    font-size: 30px;
    font-family: "Orbitron", sans-serif;
    animation: name 0.2s ease 1.3s forwards;
    opacity: 0;
}

@keyframes name {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

.name-container {
    animation: name-container 0.2s ease forwards 1s;
    padding: 0px 10px;
    margin-top: 30px;
}

@keyframes name-container {
    0% {
        background-color: #2BBC94;
    }
    30% {
        background-color: #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
        transform: skewX(60deg);
    }
    50% {
        background-color: rgba(5, 231, 151, 0);
        box-shadow: 0px 0px 7px 2px rgba(5, 231, 151, 0);
        transform: skewX(0deg);
    }
    70% {
        background-color: #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    100% {
        background-color: rgba(5, 231, 151, 0);
        box-shadow: 0px 0px 7px 2px rgba(5, 231, 151, 0);
    }
}


