.lines {
    position: absolute;
    height: 50px;
    width: 70px;
    top: 80px;
    left: 100px;
}

.lines .line {
    width: 0px;
    height: 3px;
    background-color: #2BBC94;
    margin-bottom: 20px;
    position: relative;
}

.lines .line:nth-child(1) {
    animation: linesAppear 0.7s ease 1.9s;
}

.lines .line:nth-child(2) {
    animation: linesAppear 0.7s ease 2.1s;
}

.lines .line:nth-child(3) {
    animation: linesAppear 0.7s ease 2.3s;
}

@keyframes linesAppear {
    0% {
        width: 0px;
        height: 3px;
        opacity: 0;
    }
    50% {
        width: 100%;
        height: 3px;
        box-shadow: 0px 0px 7px 2px #2BBC94;
        opacity: 1;
    }
    80% {
        width: 100%;
        height: 3px;
        box-shadow: 0px 0px 7px 2px #2BBC94;
        opacity: 1;
    }
    100% {
        width: 100%;
        height: 3px;
        box-shadow: 0px 0px 7px 2px #2BBC94;
        opacity: 0;
    }
}


