.chain {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.chain .chain-f {
    border: 3px solid #2BBC94;
    height: 50px;
    width: 50px;
    transform: rotate(45deg);
    opacity: 0;
}

.chain .chain-f:nth-child(2) {
    animation: appearShadow 0.5s ease 2.8s forwards;
}

.chain .chain-f:nth-child(1) {
    animation: appearShadow 0.5s ease 3s forwards;
}

.chain .chain-f:nth-child(3) {
    animation: appearShadow 0.5s ease 3.2s forwards;
}

@keyframes appearShadow {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    65% {
        opacity: 1;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    67% {
        opacity: 0;
        box-shadow: 0px 0px 7px 2px rgba(5, 231, 151, 0);
    }
    68% {
        opacity: 1;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    100% {
        opacity: 1;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
}

.inside-chain {
    width: 20px;
    height: 20px;
    border-left: 3px solid #2BBC94;
    border-top: 3px solid #2BBC94;
    border-left: 0;
    border-bottom: 0;
    box-shadow: 0;
    transform: rotate(45deg);
    animation: borders 0.7s ease 3s forwards;
    transform: rotate(0deg) translate(10px, 10px);
}

@keyframes borders {
    0% {
        border-left: 3px solid #2BBC94;
        border-top: 3px solid #2BBC94;
        border-right: 0;
        border-bottom: 0;
    }
    50% {
        border-left: 0;
        border-top: 3px solid #2BBC94;
        border-right: 0;
        border-bottom: 3px solid #2BBC94;
    }
    100% {
        border-left: 0;
        border-top: 0;
        border-right: 2px solid #2BBC94;
        border-bottom: 2px solid #2BBC94;
    }
}

