loader {
    display: block;
    position: relative;
    height: 40px;
    min-width: 40px;
    padding: 0 20px;
    font-size: 18px;
    line-height: 40px;
    background-color: #fff;
    left: 90vh;
    top: 12vh;
}

loader:after {
    position: absolute;
    content: "";
    top: 15px;
    left: 0;
    right: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
    -webkit-filter: blur(15px);
    filter: blur(15px);
    background: -webkit-gradient( linear, right top, left top, from(#8426af), color-stop(#ff5770), to(#6501de));
    background: linear-gradient( to left, #05e736, #8aff7f, #2BBC94);
    background-size: 200% 200%;
    -webkit-animation: animateGlow 1s linear infinite;
    animation: animateGlow 1s linear infinite;
}

@-webkit-keyframes animateGlow {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 200% 50%;
    }
}

@keyframes animateGlow {
    0% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 200% 50%;
    }
}

.css-card {
    animation: glitch-css 2s ease infinite;
}

@keyframes glitch-css {
    0% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    10% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    13% {
        border: 1px solid rgba(5, 231, 151, 0.2);
        box-shadow: 0px 0px 7px 2px rgba(5, 231, 151, 0.1);
    }
    12% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    15% {
        border: 1px solid rgba(5, 231, 151, 0.2);
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    16% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    52% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    55% {
        border: 1px solid rgba(5, 231, 151, 0.2);
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    56% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
    100% {
        border: 1px solid #2BBC94;
        box-shadow: 0px 0px 7px 2px #2BBC94;
    }
}

